<template>
  <div class="about">
    <p @click="show = !show">点击</p>
    <!-- <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter"
      @before-leave="beforeLeave"
      @leave="leave"
      @after-leave="afterLeave"
    > -->
    <FunctionalComponent>
      <ul v-show="show" class="bbb">
        <li>222222222</li>
        <li>222222222</li>
        <li>222222222</li>
        <li>222222222</li>
        <li>222222222</li>
        <li>222222222</li>
        <li>222222222</li>
      </ul>
    </FunctionalComponent>
    <!-- </transition> -->
  </div>
</template>
<script>
import { ref } from "vue";
import FunctionalComponent from "../utils/donghua.js";
export default {
  components: { FunctionalComponent },
  setup() {
    const show = ref(false);
    const transitionStyle = "0.3s height ease-in-out";
    // 进入前 设置el元素的transition,高度0
    const beforeEnter = function (el) {
      // console.log(el.dataset.data)
      el.style.transition = transitionStyle;
      el.style.height = 0;
    };
    // 进入中
    const enter = function (el) {
      // 获取元素显示的高度
      if (el.scrollHeight !== 0) {
        el.style.height = `${el.scrollHeight}px`;
      } else {
        //没有高度就不显示
        el.style.height = "";
      }
      el.style.overflow = "hidden";
    };
    // 进入结束
    const afterEnter = function (el) {
      console.log(111);
      // 清除过度样式
      // el.style.transition = ''
      el.style.height = "";
    };
    // 离开前
    const beforeLeave = function (el) {
      el.style.height = `${el.scrollHeight}px`;
      el.style.overflow = "hidden";
    };
    // 离开中
    const leave = function (el) {
      if (el.scrollHeight !== 0) {
        el.style.transition = transitionStyle;
        el.style.height = 0;
      }
    };
    // 离开结束
    const afterLeave = function (el) {
      el.style.transition = "";
      el.style.height = "";
    };
    return {
      afterLeave,
      leave,
      beforeLeave,
      afterEnter,
      enter,
      beforeEnter,
      show,
    };
  },
};
</script>
<style lang="less" scoped>
.bbb {
  position: absolute;
  top: 300px;
  left: 200px;
}
</style>
